/* SEBA: Sign UP */

.flex,
.headerLogo {
   display: flex;
   justify-content: center;
   align-items: center;
}

.headerLogo {
   height: 10vh;
   /* background-color: #8646d4; */
   border-bottom: 1px solid #3022428f;
   color: white;
   font-size: 3vmin;
}

.main-sign {
   width: 35%;
   display: grid;
   grid-template-columns: repeat(1, 1fr);
   gap: 3vmin;
   margin: 4vh 0;
}

.main-sign h2 {
   font-size: 6vmin;
   font-weight: 600;
   text-align: center;
   color: white;
}

.main-sign h2 + p {
   text-align: start;
   color: white;
}

.errorSign {
   color: #de4a34;
}

.buttonGoogle {
   height: 50px;
   border-radius: 3px;
   cursor: pointer;
   color: white;
   font-size: 2.5vmin;
   display: flex;
   align-items: center;
   padding: 0 2vmin;
}

.buttonGoogle p {
   width: 100%;
   margin-left: 16.5vmin;
   margin-bottom: 0;
}

.signupEmailOption {
   display: flex;
   align-items: center;
   justify-content: space-between;
   font-size: 2.2vmin;
   color: white;
}

.signupEmailOption p {
   margin: 0 3vmin;
}

.signupEmailOption div {
   flex: 1;
   height: 1px;
   border-bottom: 1px solid white;
}

.inputContainer {
   display: grid;
   grid-template-columns: repeat(1, 1fr);
}

.main-sign input {
   height: 40px;
   padding-left: 1vmin;
   outline: none;
   border: 1px solid white;
   border-radius: 3px;
}

.main-sign label {
   color: white;
   text-align: start;
   font-size: 2.3vmin;
   margin-bottom: 0.5vmin;
   font-weight: bolder;
   letter-spacing: 1px;
}

.adTerms {
   font-size: 1.8vmin;
   color: white;
}

.signupButtonSignup,
.signinButtonSignup {
   width: 100%;
   font-size: 2.5vmin;
   border-radius: 3px;
   height: 50px;
   font-weight: 600;
   cursor: pointer;
}
.signinButtonSignupText {
   text-align: center;
   font-weight: bold;
   font-size: 2.5vmin;
}

.signinButtonSignup,
.signinButtonSignupText {
   color: white;
}

.signinButtonSignupText span,
h2 span {
   color: #67f2cb;
}

.signupButtonSignup {
   border: none;
   color: white;
}
.signUpBody,
.signInBody {
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
}

.accountLink {
   text-decoration: none;
}
.signNoErrors,
.signErrors {
   margin-top: 0.5px;
   text-align: center;
   font-size: 2vmin;
   letter-spacing: 2px;
   margin-bottom: -0.5em;
}
.main-sign input {
   padding: 1em;
   height: 3em;
}

.signNoErrors {
   color: transparent;
}

.signErrors {
   color: red;
}
